import Vue from 'vue';
import Vuex from 'vuex';
import { internalEventBus } from './eventbus.js';

Vue.use(Vuex);

export const store = new Vuex.Store({
	state: {
    pageNumber: 0,
    numPages: 0,
    viewerScrollListeners: [],
    prevBtnDisabled: null,
    nextBtnDisabled: null
	},
	getters: {
    pageNumber: state => state.pageNumber,
    viewerScrollListeners: state => state.viewerScrollListeners,
    numPages: state => state.numPages,
    prevBtnDisabled: state => state.prevBtnDisabled,
    nextBtnDisabled: state => state.nextBtnDisabled,
  },
	mutations: {
		updatePageNumber(state, pageNumber) {
      state.pageNumber = pageNumber;
    },
    addScrollListeners(state, func) {
      state.viewerScrollListeners.push(func);
    },
    updateNumPages(state, numPages) {
      state.numPages = numPages;
    },
    updatePrevBtnDisabled(state, prevBtnDisabled) {
      state.prevBtnDisabled = prevBtnDisabled;
    },
    updateNextBtnDisabled(state, nextBtnDisabled) {
      state.nextBtnDisabled = nextBtnDisabled;
    }
	},
  actions: {
    updatePageNumber({ commit }, pageNumber) {
      commit("updatePageNumber", pageNumber);
      internalEventBus.$emit('pagenumberchanged', {
        value: pageNumber
      });
    },
    nextPage({ state, dispatch }) {
      dispatch("updatePageNumber", state.pageNumber + 1);
    },
    prevPage({ state, dispatch }) {
      dispatch("updatePageNumber", state.pageNumber - 1);
    },
    updatePageNumberPageLabel({ commit, state }, value) {
      commit("updatePageNumber", value.pageNumber);
      debugger;
      if (value.pageNumber === 1) {
        commit("updatePrevBtnDisabled", true);
      } else {
        commit("updatePrevBtnDisabled", false);
      }
      if (value.pageNumber === state.numPages) {
        commit("updateNextBtnDisabled", true);
      } else {
        commit("updateNextBtnDisabled", false);
      }
    },
    onPdfViewScroll({ state }, evt) {
      state.viewerScrollListeners.forEach(listener => {
        listener(evt);
      })
    }
  }
});